<template>
    <div class="login">
        <el-container>
            <Header></Header>
            <el-main>
                <div class="main-body">
                    <h3 class="title">个人账号登录</h3>
                    <el-form :model="loginForm" :rules="rules" ref="loginForm">
                        <div class="line">
                            <div class="info">账   号</div>
                            <el-form-item prop="username">
                                <el-input v-model="loginForm.username" placeholder="用户名"></el-input>
                            </el-form-item>
                        </div>
                        <div class="line">
                            <div class="info">密   码</div>
                            <el-form-item prop="password">
                                <el-input type="password" placeholder="密码" v-model="loginForm.password"></el-input>
                            </el-form-item>
                        </div>
                        <el-form-item>
                            <el-button type="primary" @click="submitForm('loginForm')" class="submit_btn">登陆</el-button>
                        </el-form-item>
                    </el-form>
                </div>

            </el-main>
            <el-footer height="45px">juzan.com, All rights reserved  沪ICP备18016679号</el-footer>
        </el-container>
    </div>
</template>

<script>
    import {login, store} from "../api/getdata";
    import Header from  "../components/Header"
    export default {
        name: "Login",
        data(){
            return {
                loginForm: {
                    username: '',
                    password: '',
                },
                rules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ],
                },
                showLogin: false,
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate(
                    (valid) => {
                        if (valid) {
                            let loginData = login({account: this.loginForm.username, password: this.loginForm.password})
                            loginData.then(res => {
                                store.setItem('userInfo', JSON.stringify(res.output));
                                this.$router.push("manage")
                            })
                        } else {
                            this.$message({
                                showClose: true,
                                message: '请输入正确的用户名密码',
                                type: 'error'
                            })
                        }
                    }
                )
            },
        },
        components: {
            Header
        }
    }
</script>

