<template>
    <div class="entry">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
            <el-form-item label="标的录入" prop="type">
                <el-radio v-model="form.type" label="首投">首投</el-radio>
                <el-radio v-model="form.type" label="复投">复投</el-radio>
            </el-form-item>
            <el-form-item label="平台logo" prop="logo">
                <el-upload
                        class="avatar-uploader"
                        action="https://easy-mock.com/mock/5b1143e016e1e93c6ab6fe00/admin/admin/tool/file"
                        :show-file-list="false"
                        :on-success="uploadImg"
                        :before-upload="beforeImgUpload">
                    <img v-if="form.logo" :src="form.logo" class="avatar">
                    <el-button v-else type="primary" >上传图片</el-button>
                </el-upload>
            </el-form-item>
            <div class="line">
                <el-form-item label="标的名称" prop="name">
                    <el-input v-model="form.name" placeholder="平台呢名称+期限，如宜人贷3月标"></el-input>
                </el-form-item>
                <el-form-item label="平台名称" prop="platform">
                    <el-input v-model="form.platform" placeholder="平台名称，如宜人贷"></el-input>
                </el-form-item>
            </div>
            <div class="line">
                <el-form-item label="平台评分" prop="score">
                    <el-input v-model="form.score" placeholder="评级+分数，中间加空格，如：AAA 95分"></el-input>
                </el-form-item>
                <el-form-item label="平台简述" prop="description">
                    <el-input v-model="form.description" placeholder="不超过16个字"></el-input>
                </el-form-item>
            </div>
            <div class="line">
                <el-form-item label="上线时间" prop="online">
                    <el-date-picker type="date" placeholder="上线时间" v-model="form.online" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item label="注册资金" prop="finance">
                    <el-input v-model="form.finance" placeholder="格式：xxxxW，实缴xxxx万"></el-input>
                </el-form-item>
            </div>

            <el-form-item label="标的链接" prop="projectUrl">
                <el-input v-model="form.projectUrl" placeholder="标的链接"></el-input>
            </el-form-item>
            <el-form-item label="平台标签" prop="tags">
                <el-input v-model="form.tags.join(';')" placeholder="不超过5个字，不超过8个标签。用；隔开，如：AAA级；之家3名；美股上市；"></el-input>
            </el-form-item>
            <el-form-item label="注意事项" prop="attentions">
                <el-input type="textarea" v-model="form.attentions" placeholder="不超过50个字"></el-input>
            </el-form-item>
            <el-form-item label="投资计划" class="is-required">
                <div class="plan" v-for="(plan, index) in form.plan">
                    <div class="line">
                        <el-form-item label="方案">
                            <el-input v-model="plan.name" placeholder="方案"></el-input>
                        </el-form-item>
                        <el-form-item label="期限">
                            <div class="line-info">
                                <el-input v-model="plan.timeLimit" placeholder="期限"></el-input>
                                <div class="text">天</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="充值金额">
                            <div class="line-info">
                                <el-input v-model="plan.money" placeholder="充值金额"></el-input>
                                <div class="text">元</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="出借项目">
                            <el-input v-model="plan.targetProject" placeholder="出借项目"></el-input>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <el-form-item label="聚赞返利">
                            <div class="line-info">
                                <el-input v-model="plan.giveBack" placeholder="聚赞返利"></el-input>
                                <div class="text">元</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="总收益">
                            <div class="line-info">
                                <el-input v-model="plan.sumProfit" placeholder="总收益"></el-input>
                                <div class="text">元</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="年化收益">
                            <div class="line-info">
                                <el-input v-model="plan.yearlyProfit" placeholder="年化收益"></el-input>
                                <div class="text">%</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="第几期">
                            <el-input v-model="plan.serial" placeholder="第几期"></el-input>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <el-form-item label="活动流程">
                            <div class="line-list" v-for="(info, i) in plan.planInfo" :key="i">
                                <el-input v-model="info.text" placeholder="活动流程"></el-input>
                                <div class="btn">
                                    <el-button type="primary" size="small" plain @click="addPlanInfo(index, i+1)">添加</el-button><el-button type="danger" size="small" plain @click="deletePlanInfo(index, i)" v-if="i > 0">删除</el-button>
                                </div>
                            </div>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <div class="addBtn">
                            <el-button type="success" plain @click="addPlan(index)">新增</el-button>
                            <el-button type="danger" plain @click="deletePlan(index)" v-if="index > 0">删除</el-button>
                        </div>
                    </div>
                </div>
            </el-form-item>
            <el-form-item align="center">
                <el-button type="primary" @click="onSubmit('form', '')">保存</el-button>
                <el-button type="success"  @click="$emit('open')">取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
  import {modifyMarks} from "../api/getdata"

  export default {
    name: "Entry",
    props: {
      marks: {
        default: {},
        type: Object
      },
      open: {
        default: false,
        type: Boolean
      }
    },
    data() {
      return {
        form: this.marks,
        rules: {
          type: [
            { required: true, message: '请选择首投后复投', trigger: 'blur' }
          ],
          logo: [
            { required: true, message: '请上传平台logo', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入标的名称', trigger: 'blur' }
          ],
          platform: [
            { required: true, message: '请输入平台名称', trigger: 'blur' }
          ],
          score: [
            { required: true, message: '请输入平台评分', trigger: 'blur' }
          ],
          description: [
            { required: true, message: '请输入平台简述', trigger: 'blur' }
          ],
          online: [
            { required: true,  message: '请选择上线时间', trigger: 'blur' }
          ],
          finance: [
            { required: true, message: '请输入注册资金', trigger: 'blur' }
          ],
          projectUrl: [
            { required: true, message: '请输入标的链接', trigger: 'blur' }
          ],
          tags: [
            { required: true, message: '请输入平台标签', trigger: 'blur' }
          ],
          attentions: [
            { required: true, message: '请输入注意事项', trigger: 'blur' }
          ]
        },
        isShow: this.open,
      }
    },
    methods: {
      onSubmit(ruleForm, type) {
        this.form.status = 'draft'
        let data = this.form;
        let modifyMark = modifyMarks(this.form.id, data)
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            modifyMark.then(res => {
              this.$message({
                message: '恭喜你，保存成功',
                type: 'success'
              });
              this.$emit("open")
            })
          } else {
            return false;
          }
        });
      },
      addPlan(index) {
        let plan = {
          name: '', // 方案名
          timeLimit: '', // 期限(天)
          targetProject: '', // 出借项目
          money: 0, // 充值金额(元)
          giveBack: '', // 聚赞返利(元)
          yearlyProfit: '', // 年华收益(%)
          sumProfit: '', // 总收益(元)
          serial: 0, // 第几期
          planInfo: [{
            index: 0, // 递增排序，不是序号, 序号取数组下标
            text: '', // 内容
          }]
        }
        console.log(this.form.plan[index])
        this.form.plan.push(plan)
      },
      deletePlan(index) {
        if (index > 0) {
          this.form.plan.splice(index, 1)
        } else {
          this.$message.error('已为最后一个，无法删除！');
        }
      },
      addPlanInfo(index, i) {
        let planInfo = {index: i, text: ''}
        this.form.plan[index].planInfo.push(planInfo)
        console.log(this.form.plan[index].planInfo)
      },
      deletePlanInfo(index, i) {
        if (i > 0) {
          this.form.plan[index].planInfo.splice(i, 1)
        } else {
          this.$message.error('已为最后一个，无法删除！');
        }
      },
      uploadImg(res, file) {
        if (res.output) {
          this.form.logo = res.output.path
        }else{
          this.$message.error('上传图片失败！');
        }
      },
      beforeImgUpload(file) {
        const isRightType = (file.type === 'image/jpeg') || (file.type === 'image/png');
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isRightType) {
          this.$message.error('上传图片只能是 JPG/PNG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传图片大小不能超过 2MB!');
        }
        return isRightType && isLt2M;
      },
    }
  }
</script>

<style scoped lang="sass">
    .plan
        border: 1px solid #ebebeb
        border-radius: 3px
        transition: .2s
        padding: 30px 30px 10px 10px
        margin-bottom: 10px
        .line
            padding-bottom: 20px
        .line
            display: flex
            div.el-form-item
                flex: 1
            .addBtn
                margin-left: 80px
        .line-list
            display: flex
            padding-bottom: 10px
            div:first-child
                flex: auto
            .btn
                width: 160px
                padding-left: 10px
        .line-info
            display: flex
            div:first-child
                flex: auto
            .text
                padding-left: 5px
                color: #9A9A9A
</style>