import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login'
import Manage from './views/Manage'

// page
import Entry from './views/Entry'
import Firstshot from './views/Firstshot'
import Reinvestment from './views/Reinvestment'
import Back from './views/Back'
import Ad from './views/Ad'
import User from './views/User'
import Staff from './views/Staff'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/manage',
        name: 'manage',
        redirect: '/manage/staff',
        component: Manage,
        children:[
            {
                path: 'entry',

                component: Entry
            },
            {
                path: 'firstshot',
                component: Firstshot
            },
            {
                path: 'reinvestment',
                component: Reinvestment
            },
            {
                path: 'back',
                component: Back
            },
            {
                path: 'ad',
                component: Ad
            },
            {
                path: 'user',
                component: User
            },
            {
                path: 'staff',
                component: Staff
            },
        ]
    },
  ]
})
