<template>
    <div class="staff">
        <div class="header">
            <h3>员工管理</h3>
        </div>
        <el-table
                v-loading="loading"
                :data="list"
                style="width: 100%">
            <el-table-column
                    label="工号"
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.account }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="权限等级"
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.level }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="员工职能"
                    min-width="150">
                <template slot-scope="scope">
                    <span >{{ scope.row.title }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="密码"
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.password }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="真实姓名"
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.realName }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="身份证号"
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.idCard }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="手机"
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.phone }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="210">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button
                            size="mini"
                            type="primary"
                            @click="handleAdd(scope.$index, scope.row)">添加</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page" v-show="count > 8">
            <div class="page-init"></div>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :page-size=8
                    :total="count">
            </el-pagination>
        </div>
        <el-dialog title="添加员工" :visible.sync="dialogFormVisible">
            <el-form :model="form" label-position="left">
                <el-form-item label="工号" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.account" placeholder="从0002开始"></el-input>
                </el-form-item>
                <el-form-item label="权限等级" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.level" placeholder="0"></el-input>
                </el-form-item>
                <el-form-item label="员工职称" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.title" placeholder="员工职称"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.password" placeholder="8位数字与字母组合"></el-input>
                </el-form-item>
                <el-form-item label="真实姓名" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.realName" placeholder="身份证上的名字"></el-input>
                </el-form-item>
                <el-form-item label="身份证号" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.idCard" placeholder="真实身份证号"></el-input>
                </el-form-item>
                <el-form-item label="手机号" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.phone" placeholder="手机号"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitForm">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {getStaffList, addStaff, deleteStaff, putStaff} from "../api/getdata";

    export default {
        name: "Staff",
        data() {
            return {
                list: [],
                loading: true,
                page: 1,
                count: 0,
                type: '',
                editId: null,
                dialogFormVisible: false,
                formLabelWidth: '120px',
                form: {
                    account: '', // 工号
                    password: '', // 密码
                    level: 0, // 权限级别
                    title: '', // 用户职称
                    realName: '', // 真实姓名
                    idCard: '', // 身份证号
                    phone: '', // 手机号
                },
            }
        },
        created() {
          this.userList()
        },
        methods: {
            userList() {
                let user = getStaffList({page: this.page})
                user.then(res => {
                    this.list = res.output.rows
                    this.count = res.output.count
                    this.loading = false
                })
            },
            handleCurrentChange(val) {
                this.page = val
                this.userList()
            },
            handleEdit(index, val) {
                console.log(val)
                this.dialogFormVisible = true
                this.form = val
                this.type = 'edit'
                this.editId = val.id
            },
            handleDelete(index, val) {
                this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let delUser = deleteStaff(val.id)
                    delUser.then(res => {
                        this.list.splice(index, 1)
                        console.log(res)
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                }).catch(() => {

                })

            },
            handleAdd() {
                this.dialogFormVisible = true
                this.type = 'add'
            },
            submitForm() {
                if (this.type == 'add') {
                    let addUserData = addStaff(this.form)
                    addUserData.then(res => {
                        this.dialogFormVisible = false
                        console.log(res)
                        this.$message({
                            message: '添加成功!',
                            type: 'success'
                        });
                        this.form = {}
                    })
                } else if (this.type == 'edit') {
                    let data = putStaff(this.editId, this.form)
                    data.then(res => {
                        console.log(res)
                        this.dialogFormVisible = false
                        this.$message({
                            message: '修改成功!',
                            type: 'success'
                        });
                    })
                }

            },
        }
    }
</script>

<style scoped lang="sass">
    .el-form-item
        margin-right: 20px
    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 10px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        h3
            font-size: 16px
    .el-form
        display: flex
        flex-wrap: wrap
    .staff
        .el-table td
            padding: 8px 0
</style>