<template>
    <div class="abouts">
        <div class="header">
            <h3>广告管理</h3>
        </div>

        <el-table
          v-if="adList.length > 0"
          v-loading="loading"
          :data="adList"
          style="width: 100%">
            <el-table-column
              label="序号"
              min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.id }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="图片"
              min-width="220">
                <template slot-scope="scope">
                    <span ><img :src="scope.row.image" alt=""></span>
                </template>
            </el-table-column>
            <el-table-column
              label="对应链接"
              min-width="220">
                <template slot-scope="scope">
                    <span >{{ scope.row.url }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="210">
                <template slot-scope="scope">
                    <el-button
                      v-show="scope.row.status == 'active'"
                      size="mini"
                      type="info"
                      disabled
                      >已发布</el-button>
                    <el-button
                      v-show="scope.row.status == 'off'"
                      size="mini"
                      type="success"
                      @click="handleEdit(scope.$index, scope.row)">发布</el-button>
                    <el-button
                      size="mini"
                      type="danger"
                      @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    <el-button
                      size="mini"
                      type="primary"
                      @click="handleAdd(scope.$index, scope.row)">添加</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-row :gutter="20" v-else>
            <el-col :span="12" :offset="6">
                <div class="tips">暂无广告，请添加！</div>
                <div class="ad-upload">
                    <div class="line">
                        <div class="text">图片</div>
                        <div class="upload">
                            <el-upload
                                    class="avatar-uploader"
                                    action="https://easy-mock.com/mock/5b1143e016e1e93c6ab6fe00/admin/admin/tool/file"
                                    :show-file-list="false"
                                    :on-success="uploadImg"
                                    :before-upload="beforeImgUpload">
                                <img v-if="vain.image" :src="vain.image" class="avatar">
                                <el-button v-else size="small" type="primary" round>上传图片</el-button>
                            </el-upload>
                        </div>
                    </div>
                    <div class="line">
                        <div class="text">对应链接</div>
                        <div class="form">
                            <el-input v-model="vain.url" placeholder="输入链接地址"></el-input>
                        </div>
                    </div>
                    <div class="btn">
                        <el-button type="el-button el-button--primary is-round" @click="handleAddInfo">添加</el-button>
                    </div>
                </div>
            </el-col>
        </el-row>
        <el-dialog
                title="添加广告"
                :visible.sync="centerDialogVisible"
                width="70%"
                center>
            <div class="ad-upload">
                <div class="line">
                    <div class="text">图片</div>
                    <div class="upload">
                        <el-upload
                                class="avatar-uploader"
                                action="https://easy-mock.com/mock/5b1143e016e1e93c6ab6fe00/admin/admin/tool/file"
                                :show-file-list="false"
                                :on-success="uploadImg"
                                :before-upload="beforeImgUpload">
                            <img v-if="vain.image" :src="vain.image" class="avatar">
                            <el-button v-else size="small" type="primary" round>上传图片</el-button>
                        </el-upload>
                    </div>
                </div>
                <div class="line">
                    <div class="text">对应链接</div>
                    <div class="form">
                        <el-input v-model="vain.url" placeholder="输入链接地址"></el-input>
                    </div>
                </div>
                <div class="btn">
                    <el-button type="el-button el-button--primary is-round" @click="handleAddInfo">添加</el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {getBannerList, deleteBanner, putBanner, getBannerFile, uploadBanner} from "../api/getdata"

    export default {
        name: "Ad",
        data() {
          return {
            loading: true,
            adList: [],
            vain: {
                id: 0,
                image: '',
                index: 0,
                status: "off",
                url: ""
            },
            centerDialogVisible: false
          }
        },
        created() {
          this.getBanner()
        },
        methods: {
            getBanner() {
              let bannerList = getBannerList()
              bannerList.then(res => {
                console.log(res)
                this.loading = false
                this.adList = res.output
              })
            },
            handleDelete(index, val) {
              this.$confirm('此操作将永久删除此广告, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                let del = deleteBanner(val.id)
                del.then(res => {
                  this.adList.splice(index, 1)
                  console.log(res)
                  this.$message({
                    type: 'success',
                    message: '删除成功!'
                  });
                })
              }).catch(() => {

              })
            },
          handleEdit(index, val) {
                let bannerState = putBanner(val.id, {status: 'active'})
                this.$confirm('广告将会发布至前台, 是否发布?', '', {
                  confirmButtonText: '确定',
                  cancelButtonText: '取消',
                  type: 'warning'
                }).then(() => {
                  bannerState.then(res => {
                    console.log(res)
                    this.adList[index].status = 'active'
                    console.log(this.adList[index].status)
                    this.$message({
                      message: '恭喜你，广告发布至前台成功!',
                      type: 'success'
                    });
                  })
                }).catch(() => {

                });

          },
          handleAdd(index, val) {
            console.log(index, val)
            this.centerDialogVisible = true
          },
          uploadImg(res, file) {
            if (res.output) {
              this.vain.image = res.output.path
            }else{
              this.$message.error('上传图片失败！');
            }
          },
          beforeImgUpload(file) {
            const isRightType = (file.type === 'image/jpeg') || (file.type === 'image/png');
            const isLt2M = file.size / 1024 / 1024 < 2;

            if (!isRightType) {
              this.$message.error('上传图片只能是 JPG/PNG 格式!');
            }
            if (!isLt2M) {
              this.$message.error('上传图片大小不能超过 2MB!');
            }
            return isRightType && isLt2M;
          },
          handleAddInfo() {
            let result = {
              image: this.vain.image,
              url: this.vain.url,
              index: this.vain.index,
              status: this.vain.status
            }
            if (this.vain.image && this.vain.url) {
              let upload = uploadBanner(result)
              upload.then(res => {
                this.$message({
                  message: '恭喜你，广告添加成功!',
                  type: 'success'
                });
                this.adList = this.adList.concat(this.vain)
                this.vain = {id: ++this.vain.id ,image: '',index: 0,status: "off",url: ""}
                this.centerDialogVisible = false
              })
            } else {
              this.$message.error('请上传图片或输入对应链接');
            }

          }
        }
    }
</script>

