<template>
    <div class="reinvest">
        <div class="header">
            <h3>首投标的管理</h3>
            <div class="header-select">
                <el-radio-group v-model="tab" size="small">
                    <el-radio-button label="默认"></el-radio-button>
                    <el-radio-button label="在架"></el-radio-button>
                    <el-radio-button label="已下架"></el-radio-button>
                </el-radio-group>
            </div>
        </div>
        <el-table
                v-show="tab == '默认'"
                v-loading="loading"
                :data="list"
                row-key="id"
                highlight-current-row
                style="width: 100%">
            <el-table-column
                    label="标的名称"
                    min-width="210">
                <template slot-scope="scope">
                    <span class="el-icon-sort">  {{  scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="录入时间"
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.createdAt }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="录入人"
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.createrName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="200">
                <template slot-scope="scope">
                    <el-button
                            v-if="scope.row.status == 'draft' || scope.row.status == 'off' && tab == '默认'"
                            size="mini"
                            plain
                            type="primary"
                            @click="handlePutMarks(scope.$index, scope.row, 'active')">发布</el-button>
                    <el-button
                            v-if="scope.row.status == 'active'"
                            size="mini"
                            type="info"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)">已发布</el-button>
                    <el-button
                            v-if="tab == '默认'"
                            size="mini"
                            type="danger"
                            plain
                            @click="handleEditMarks(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="warning"
                            plain
                            v-if="scope.row.status == 'active' || scope.row.status == 'draft'"
                            @click="handlePutMarks(scope.$index, scope.row, 'off')">下架</el-button>
                    <el-button
                            size="mini"
                            type="info"
                            v-if="scope.row.status == 'off'"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)">已下架</el-button>
                    <el-button
                            size="mini"
                            type="success"
                            plain
                            v-if="(scope.row.status == 'off' && tab == '已下架') || (scope.row.status == 'off' && tab == '在架') "
                            @click="handlePutMarks(scope.$index, scope.row, 'off')">上架</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            plain
                            v-if="tab == '已下架'"
                            @click="handleDeleteMarks(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-table
                v-show="tab == '在架' || tab == '已下架'"
                v-loading="loading"
                :data="list"
                row-key="id"
                highlight-current-row
                style="width: 100%">
            <el-table-column
                    label="标的名称"
                    min-width="210">
                <template slot-scope="scope">
                    <span>  {{  scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="录入时间"
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.createdAt }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="录入人"
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.createrName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="200">
                <template slot-scope="scope">
                    <el-button
                            v-if="scope.row.status == 'draft' || scope.row.status == 'off' && tab == '默认'"
                            size="mini"
                            plain
                            type="primary"
                            @click="handlePutMarks(scope.$index, scope.row, 'active')">发布</el-button>
                    <el-button
                            v-if="scope.row.status == 'active'"
                            size="mini"
                            type="info"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)">已发布</el-button>
                    <el-button
                            v-if="tab == '默认'"
                            size="mini"
                            type="danger"
                            plain
                            @click="handleEditMarks(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="warning"
                            plain
                            v-if="scope.row.status == 'active' || scope.row.status == 'draft'"
                            @click="handlePutMarks(scope.$index, scope.row, 'off')">下架</el-button>
                    <el-button
                            size="mini"
                            type="info"
                            v-if="scope.row.status == 'off'"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)">已下架</el-button>
                    <el-button
                            size="mini"
                            type="success"
                            plain
                            v-if="(scope.row.status == 'off' && tab == '已下架') || (scope.row.status == 'off' && tab == '在架') "
                            @click="handlePutMarks(scope.$index, scope.row, 'off')">上架</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            plain
                            v-if="tab == '已下架'"
                            @click="handleDeleteMarks(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="外层 Dialog" :visible.sync="editShow" width="70%">
            <EditMarks :marks="editMarks" @open="showDialog"></EditMarks>
        </el-dialog>

        <div class="page" v-show="count >= 10">
            <div class="page-init">
                <div class='show-d'>默认 : {{ oldList}}</div>
                <div class='show-d'>新 : {{newList}}</div>
            </div>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :page-size=10
                    :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
  import {getMarks, putChangeMarksShelf, deleteMarks, getMarksDetail} from "../api/getdata"
  import Sortable from 'sortablejs'
  import EditMarks from '../components/EditMarks'
  export default {
    name: "Reinvestment",
    data() {
      return {
        loading: true,
        list: [],
        oldList: [],
        newList: [],
        count: 0,
        page: 0,
        sortable: null,
        tab: '默认',
        editShow: false,
        editMarks: {}
      }
    },
    created() {
      this.getReinvestmentData({type: '首投'})
    },
    methods: {
      getReinvestmentData(data) {
        let ReinvestData = getMarks(data)
        ReinvestData.then(res => {
          console.log(res)
          this.list = res.output.rows
          this.count = res.output.count
          this.loading = false
          this.oldList = this.list.map(v => v.index)
          this.newList = this.oldList.slice()
          this.$nextTick(() => {
            this.setSort()
          })
        })
      },
      setSort() {
        const el = document.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
        this.sortable = Sortable.create(el, {
          ghostClass: 'sortable-ghost',
          onEnd: evt => {
            const targetRow = this.list.splice(evt.oldIndex, 1)[0]
            this.list.splice(evt.newIndex, 0, targetRow)
            const tempIndex = this.newList.splice(evt.oldIndex, 1)[0]
            this.newList.splice(evt.newIndex, 0, tempIndex)
          }
        })
      },
      handleCurrentChange(val) {
        this.page = val
        this.loading = true
        this.getReinvestmentData({index: this.page})
      },
      handleRefuse(index, val) {
        console.log(index, val)
        this.$prompt('请输入拒绝返现备注', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
        }).then(({ value }) => {
          let setBack = setGiveBack(val.id, {status: '未通过', rejectReason: value})
          setBack.then(res => {
            console.log(res)
            this.list[index].status = '未通过'
            this.$message({
              type: 'success',
              message: '返现备注为: ' + value
            });
          })

        })
      },
      // 发布
      handlePutMarks(index, val, status) {
        console.log(index, val)
        let message = status == 'active' ? '发布后的标将在前台显示' : '下架后的标将不在前台展示,进入已下架列表'
        this.$confirm(message+', 是否继续?', '', {
          confirmButtonText: status == 'active' ? '发布' : '下架',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let putMarks = putChangeMarksShelf(val.id, {status: status})
          putMarks.then(res => {
            console.log(res)
            this.list[index].status = status
            this.$message({
              type: 'success',
              message: status == 'active' ? '发布成功!' : '下架成功!'
            })
          })
        }).catch(() => {

        });


      },
      handleDeleteMarks(index, val) {
        let marks = deleteMarks(index)
        this.$confirm('此操作将永久删除该标, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          marks.then(res => {
            this.list.splice(index, 1)
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          })

        })
      },
      handleEditMarks(index, val) {
        let getMarks = getMarksDetail(val.id)
        getMarks.then(res => {
          console.log(res)
          this.editMarks = res.data
          this.editShow = true
        })

      },
      showDialog: function () {
        this.editShow = !this.editShow
      }
    },
    components: {
      EditMarks
    },
    watch: {
      tab() {
        if (this.tab == '默认') {
          this.getReinvestmentData({type: '首投'})
        } else if (this.tab == '在架') {
          this.getReinvestmentData({type: '首投', status: 'active'})
        } else if (this.tab == '已下架') {
          this.getReinvestmentData({type: '首投', status: 'off'})
        }
      }
    }
  }
</script>

<style scoped lang="sass">

    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 20px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        display: flex
        align-items: center
        justify-content: space-between
        .header-select
            margin-right: 30px
        .search
            display: flex
            width: 450px
            .el-input
                margin-right: 8px
        h3
            font-size: 16px
</style>