<template>
    <div class="staff">
        <div class="header">
            <h3>员工管理</h3>
        </div>
        <el-table
          v-loading="loading"
          :data="userList"
          style="width: 100%">
            <el-table-column
              label="用户名"
              min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.source }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="注册时间"
              min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.createdAt }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="常用手机数量"
              min-width="90">
                <template slot-scope="scope">
                    <span >{{ scope.row.contactNum }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="注册平台数量"
              min-width="90">
                <template slot-scope="scope">
                    <span >{{ scope.row.caseNum }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="累计投资额度(w)"
              min-width="110">
                <template slot-scope="scope">
                    <span >{{ scope.row.investNum }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="支付宝账号"
              min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.account }}</span>
                </template>
            </el-table-column>
            <el-table-column
              label="支付宝实名"
              min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.realName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="210">
                <template slot-scope="scope">
                    <el-rate :colors="['#ff3333', '#ff3333', '#ff3333']" v-model="scope.row.star" @change="handleEdit(scope.$index, scope.row)"></el-rate>
                </template>
            </el-table-column>
        </el-table>
        <div class="page" v-show="count > 8">
            <div class="page-init"></div>
            <el-pagination
              background
              layout="prev, pager, next"
              @current-change="handleCurrentChange"
              :page-size=8
              :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {getUserList, addUserStart} from "../api/getdata"

    export default {
        name: "User",
        data() {
          return {
            loading: true,
            starts: 0,
            count: 0,
            userList: [],
            page: 0
          }
        },
        created() {
          this.getUserData()
        },
        methods: {
          handleCurrentChange(val) {
            this.page = val
            this.loading = true
            this.getUserData()
          },
          getUserData() {
            let userList = getUserList({page: this.page})
            userList.then(res => {
              console.log(res.output)
              this.loading = false
              this.count = res.output.count
              this.userList = res.output.rows
            }).catch(() => {
              this.$message({
                type: 'error',
                message: '加载错误,请刷新页面!'
              })
            })
          },
          handleEdit(index, val) {
            console.log(index, val)
            this.$confirm('此操作将永久改变用户评分, 是否继续?', '', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              let userStart = addUserStart(val.id, val.star)
              userStart.then(res => {
                console.log(res)
                this.$message({
                  type: 'success',
                  message: '更改成功!'
                })
              })
            }).catch(() => {

            });
          }
        }
    }
</script>

<style scoped lang="sass">
    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 10px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        h3
            font-size: 16px
</style>