<template>
    <el-header>
        <div class="logo container">
            <img src="../assets/logo.png" alt="">
            <h1>聚赞管理中心</h1>
            <div class="user" v-if="userInfo">
                <span>{{userInfo.title}}</span>
                <span>{{userInfo.realName}}</span>
                <span @click="goLogout" class="logout">退出</span>
            </div>
        </div>
    </el-header>
</template>

<script>
    import {logout, store} from "../api/getdata";

    export default {
        name: "Header",
        props: {
            userInfo: {}
        },
        methods: {
            goLogout() {
                let data = logout()
                data.then(res => {
                    if (res) {
                        this.$message({
                            message: '退出成功',
                            type: 'success'
                        });
                        store.removeItem("userInfo")
                        this.$router.push("/login")
                    } else {
                        console.log('logout')
                    }
                })
            }
        }
    }
</script>

<style scoped lang="sass">
    .el-header

        display: flex
        align-items: center
        background-color: #fff
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.03)
        .logo
            display: flex
            align-items: center
            width: 100%
            h1
                flex: auto
                font-weight: normal
                font-size: 16px
                padding-left: 15px
                color: #9A9A9A
    .user
        font-size: 14px
        span
            padding-right: 10px
