<template>
    <div class="giveback">
        <div class="header">
            <h3>返现管理</h3>
            <div class="search">
                <el-input
                    v-model="search"
                    placeholder="标的名称"
                    suffix-icon="el-icon-search"
                    clearable
                ></el-input><el-button type="primary" :disabled="search.length == 0" @click="querySearch">搜索</el-button>
            </div>
            <div class="header-select">
                <el-radio-group v-model="tab" size="small">
                    <el-radio-button label="默认"></el-radio-button>
                    <el-radio-button label="已返现"></el-radio-button>
                    <el-radio-button label="未返现"></el-radio-button>
                </el-radio-group>
            </div>
        </div>
        <el-table
                v-loading="loading"
                :data="list"
                style="width: 100%">
            <el-table-column
                    label="标的名称"
                    min-width="150">
                <template slot-scope="scope">
                    <span >{{ scope.row.project.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="所选方案"
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.plan.id }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="出借日期"
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.loanDate }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="返现金额"
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.giveBack }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="注册手机号"
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.phone }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="投资人姓名"
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="支付宝账号"
                    min-width="160">
                <template slot-scope="scope">
                    <span >{{ scope.row.account }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="支付宝实名"
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.realName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" min-width="210">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            type="info"
                            v-if="scope.row.status == '已通过'"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)">已返现</el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            type="primary"
                            @click="handleGiveBack(scope.$index, scope.row)">返现</el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            type="danger"
                            @click="handleRefuse(scope.$index, scope.row)">拒绝</el-button>
                    <el-button
                            v-if="scope.row.status == '未通过'"
                            size="mini"
                            type="warning"
                            plain
                            disabled
                            @click="handleRefuse(scope.$index, scope.row)">已拒绝</el-button>
                    <el-button
                            v-if="scope.row.status == '审核中'"
                            size="mini"
                            disabled
                            @click="handleRefuse(scope.$index, scope.row)">审核中</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page" v-show="count >= 10">
            <div class="page-init"></div>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :page-size=10
                    :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {getGiveBack, setGiveBack} from "../api/getdata"

    export default {
        name: "Back",
        data() {
          return {
            loading: true,
            list: [],
            count: 0,
            search: '',
            tab: '默认'
          }
        },
        created() {
          this.getGiveBackData()
        },
        methods: {
          getGiveBackData(data) {
            let backData = getGiveBack(data)
            backData.then(res => {
              console.log(res)
              this.list = res.output.rows
              this.count = res.output.count
              this.loading = false
            })
          },
          handleRefuse(index, val) {
            console.log(index, val)
            this.$prompt('请输入拒绝返现备注', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
            }).then(({ value }) => {
              let setBack = setGiveBack(val.id, {status: '未通过', rejectReason: value})
              setBack.then(res => {
                console.log(res)
                this.list[index].status = '未通过'
                this.$message({
                  type: 'success',
                  message: '拒绝返现备注为: ' + value
                });
              })

            })
          },
          handleGiveBack(index, val) {
            console.log(index, val)
            this.$confirm('确认线下已返现, 是否继续?', '', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              let setBack = setGiveBack(val.id, {status: '已通过'})
              setBack.then(res => {
                console.log(res)
                this.list[index].status = '已通过'
                this.$message({
                  type: 'success',
                  message: '已确认返现!'
                })
              })
            }).catch(() => {

            });


          },
          handleCurrentChange(val) {
            this.page = val
            this.loading = true
            this.getGiveBackData({index: this.page})
          },
          querySearch() {
              this.getGiveBackData({name: this.search})
          }
        },
        watch: {
          tab() {
            if (this.tab == '默认') {
                this.getGiveBackData()
            } else if (this.tab == '已返现') {
                this.getGiveBackData({status: '已返现'})
            }else if (this.tab == '未返现') {
                this.getGiveBackData({status: '未返现'})
            }
          }
        }
    }
</script>

<style scoped lang="sass">
    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 20px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        display: flex
        align-items: center
        justify-content: space-between
        .header-select
            margin-right: 30px
        .search
            display: flex
            width: 450px
            .el-input
                margin-right: 8px
        h3
            font-size: 16px
</style>