import {fetch, post, put, deletes} from "../util/http"


/**
 * store
 * @type {Storage}
 */
export const store = window.localStorage

/**
 * 后台登录
 * @param data
 * @returns {Promise<any>}
 */
export const login = data => post("/admin/employee/login", data)

/**
 * 后台登出
 * @returns {Promise<any>}
 */
export const logout = () => fetch("/admin/employee/logout")

/**
 * 获取员工列表
 * @returns {Promise<any>}
 */
export const getStaffList = (data) => fetch("/admin/employee/list", data)

/**
 * 新增员工
 * @param data
 * @returns {Promise<any>}
 */
export const addStaff = (data) => post("/admin/employee", data)

/**
 * 删除员工
 * @param data
 */
export const deleteStaff = (data) => deletes(`/admin/employee/${data}`)


/**
 * 修改员工信息
 * @param data
 */
export const putStaff = (id, data) => put(`/admin/employee/${id}`, data)


/**
 * 获取用户列表
 * @returns {Promise<any>}
 */
export const getUserList = () => fetch("/admin/user/list")


/**
 * 为用户星级打分
 * @param data
 * @returns {Promise<any>}
 */
export const addUserStart = (data) => post(`/admin/user/${data}/star`)


/**
 * 获取广告列表
 * @returns {Promise<any>}
 */
export const getBannerList = () => fetch("/admin/banner/list")

/**
 * 删除广告
 * @param id
 * @returns {*}
 */
export const deleteBanner = (id) => deletes(`/admin/banner/${id}`)

/**
 * 修改广告状态
 * @param id
 * @param data
 * @returns {Promise<any>}
 */
export const putBanner = (id, data) => put(`/admin/banner/${id}`, data)

/**
 * 获取文件 for 管理后台
 * @returns {Promise<any>}
 */
export const getBannerFile = () => fetch("/admin/tool/file")

/**
 * 新增广告
 * @param data
 * @returns {Promise<any>}
 */
export const uploadBanner = (data) => post("/admin/banner", data)


/**
 * 获取返现列表
 * @returns {Promise<any>}
 */
export const getGiveBack  = (data) => fetch("/admin/case/list", data)

/**
 * 设置返现标
 * @param id
 * @param data
 * @returns {Promise<any>}
 */
export const setGiveBack = (id, data) => post(`/admin/case/${id}`, data)

/**
 * 获取标列表
 * @param data
 * @returns {Promise<any>}
 */
export const getMarks = (data) => fetch("/admin/project/list", data);

/**
 * 修改标的状态（上下架）
 * @param id
 * @param data
 * @returns {Promise<any>}
 */
export const putChangeMarksShelf = (id, data) => put(`/admin/project/${id}/status`, data)

/**
 * 创建标
 * @param data
 * @returns {Promise<any>}
 */
export const createMarks = (data) => post("/admin/project", data)

/**
 * 修改标的信息
 * @param id
 * @param data
 * @returns {Promise<any>}
 */
export const modifyMarks = (id, data) => put(`/admin/project/${id}`, data)


/**
 * 删除标
 * @param id
 */
export const deleteMarks = id => deletes(`/admin/project/${id}`)

/**
 * 获取标详情
 * @param id
 * @returns {Promise<any>}
 */
export const getMarksDetail = id => fetch(`/admin/projects/${id}`)